import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/cepPais';

const CepPaisIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'codigoIBGE',
            text: 'Código IBGE',
          },
          {
            dataField: 'sigla',
            text: 'Sigla',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'CepPaisSearchView',
      }}
    />
  );
};

export default CepPaisIS;
