import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  EditForm,
  EditViewProps,
  ERROR,
  findByExample,
  getSelectedItem,
  InputGroupInline,
  InputType,
  onlyAlpha,
  onlyNumber,
  showToast,
  WindowComponent,
} from 'summer';
import { CepUF, CLASS_NAME } from '../../../../models/dtos/ger/cepUF';
import CepPaisIS from '../../../components/ger/cepPaisIS/CepPaisIS';

interface CepUFEditViewProps extends EditViewProps<CepUF> {}

const CepUFEditView: React.FC<CepUFEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CepUF>({ defaultValues: selectedItem, mode: 'onBlur' });

  const verificarCodigo = async () => {
    const cepUfExample: CepUF = {
      codigo: formProps.getValues().codigo,
    };
    if (cepUfExample.codigo) {
      findByExample(cepUfExample, CLASS_NAME, {
        errorFunction: mensagem => {
          showToast(mensagem, ERROR);
        },
        thenFunction: result => {
          if (result) {
            const params: AlertProps = {
              message: `Código ${cepUfExample.codigo} já cadastrado. Deseja contiunar?`,
              onNoClick: () => {
                formProps.setValue('codigo', '');
              },
              type: AlertType.WARNING_YES_NO,
            };
            alertManager.emit(ACTION_EVENTS.SHOW, params);
          }
        },
      });
    }
  };

  return (
    <WindowComponent title="Manutenção de UF">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              inputProps={true}
              label="Código"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 2), required: true } }}
              isPrependLabel={true}
              onBlur={verificarCodigo}
              placeholder="Informe o código"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>

        <Row>
          <Col>
            <InputGroupInline
              id="sigla"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, ...onlyAlpha(0, 2) } }}
              label="Sigla"
              placeholder="Informe a Sigla"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="nome"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 20 } }}
              label="Nome"
              placeholder="Informe o Nome"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepPaisIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="País"
              placeholder="Selecione um País"
              id="pais"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <CustomInput type="switch" id="exterior" name="exterior" label="UF é do exterior?" innerRef={formProps.register} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CepUFEditView;
